import React, { Component } from 'react';
import { Badge, Card, CardBody, CardHeader, Col, Row, Table, Modal, ModalBody, ModalHeader } from 'reactstrap';
import { Redirect } from 'react-router-dom';
import Cookies from 'universal-cookie';
const cookies = new Cookies();

class ChatList extends Component {
  constructor(props) {
    super(props);

    this.state = {
      chats: [],
      isLoggedIn: false,
      modalOpen: false,
      age: '',
      email: '',
      name: '',
      fin1: '',
      fin2: '',
      fin3: '',
      lgl1: '',
      lgl2: '',
      lgl3: '',
      lgl4: '',
      med1: '',
      med2: '',
      med3: '',
      rsa1: '',
      rsa2: '',
      rsa3: '',
    };

    this.toggleModal = this.toggleModal.bind(this);
  }

  toggleModal(chat) {
    console.log("CHAT",chat);
    
    this.setState({
      age: chat.age === undefined ? '' : chat.age.value,
      email: chat.email === undefined ? '' : chat.email.value,
      name: chat.name === undefined ? '' : chat.name.value,
      fin1: chat.FIN1 === undefined ? '' : chat.FIN1.value,
      fin2: chat.FIN2 === undefined ? '' : chat.FIN2.value,
      fin3: chat.FIN3 === undefined ? '' : chat.FIN3.value,
      lgl1: chat.LGL1 === undefined ? '' : chat.LGL1.value,
      lgl2: chat.LGL2 === undefined ? '' : chat.LGL2.value,
      lgl3: chat.LGL3 === undefined ? '' : chat.LGL3.value,
      lgl4: chat.LGL4 === undefined ? '' : chat.LGL4.value,
      med1: chat.MED1 === undefined ? '' : chat.MED1.value,
      med2: chat.MED2 === undefined ? '' : chat.MED2.value,
      med3: chat.MED3 === undefined ? '' : chat.MED3.value,
      rsa1: chat.RSA1 === undefined ? '' : chat.RSA1.value,
      rsa2: chat.RSA2 === undefined ? '' : chat.RSA2.value,
      rsa3: chat.RSA3 === undefined ? '' : chat.RSA3.value,
      modalOpen: !this.state.modalOpen,
    });
  }

  fetchChats() {
    fetch('https://missionaustralia-back.herokuapp.com/getChats?token=' + cookies.get('token'))
      .then(response => response.json())
      .then(resData => {
        console.log(resData.data)
        if (resData.response === 200) {
          this.setState({
            chats: resData.data
          });
        } else {
          alert("Error fetching chats")
        }
      });
  }

  componentWillMount() {
    if (cookies.get('token') !== undefined && cookies.get('token') !== null) {
      this.setState({
        isLoggedIn: true
      });
    }
  }

  componentDidMount() {
    this.fetchChats()
  }

  render() {
    if (this.state.isLoggedIn) {
      return (
        <div className="animated fadeIn">
          <Row>
            <Col>
              <Card>
                <CardHeader>
                  <i className="fa fa-align-justify"></i> Chats List
              </CardHeader>
                <CardBody>
                  <Table hover bordered striped responsive size="sm">
                    <thead>
                      <tr>
                        <th>Name</th>
                        <th>Age</th>
                        <th>Email</th>
                        <th>Details</th>
                      </tr>
                    </thead>
                    <tbody>
                      {this.state.chats.map((chat, i) => {
                        return (
                          <tr key={chat._id}>
                            <td>{chat.name.value}</td>
                            <td>{chat.age.value}</td>
                            <td>{chat.email.value}</td>
                            <td>
                              <a  style={{cursor: 'pointer'}} onClick={()=>{this.toggleModal(chat)}}>Details</a>
                            </td>
                          </tr>);
                      })}
                    </tbody>
                  </Table>
                </CardBody>
              </Card>
            </Col>
          </Row>

          <Modal isOpen={this.state.modalOpen} toggle={this.toggleModal}
          className={'modal-primary ' + this.props.className}>
          <ModalHeader>Chat </ModalHeader>
          <ModalBody>
            <table style={{width: '100%'}}>
              <tbody>
                <tr>
                  <td><b>Name : </b></td>
                  <td>{this.state.name}</td>
                </tr>
                <tr>
                  <td><b>Age : </b></td>
                  <td>{this.state.age}</td>
                </tr>
                <tr>
                  <td><b>Email : </b></td>
                  <td>{this.state.email}</td>
                </tr>
                <tr>
                  <td><b>Are you doing any Job? : </b></td>
                  <td>{this.state.fin1}</td>
                </tr>
                <tr>
                  <td><b>Is it a part time job or full time job? : </b></td>
                  <td>{this.state.fin2}</td>
                </tr>
                <tr>
                  <td><b>Is it sufficient you to survive yourself? : </b></td>
                  <td>{this.state.fin3}</td>
                </tr>
                <tr>
                  <td><b>Are you having your own or rented property? : </b></td>
                  <td>{this.state.rsa1}</td>
                </tr>
                <tr>
                  <td><b>Are you having any accommodation problem? : </b></td>
                  <td>{this.state.rsa2}</td>
                </tr>
                <tr>
                  <td><b>Could you please briefly explain the situation? : </b></td>
                  <td>{this.state.rsa3}</td>
                </tr>
                <tr>
                  <td><b>Are you facing any domestic violence? : </b></td>
                  <td>{this.state.lgl1}</td>
                </tr>
                <tr>
                  <td><b>Could you please briefly explain the situation? : </b></td>
                  <td>{this.state.lgl2}</td>
                </tr>
                <tr>
                  <td><b>Are you facing any family breakdown problem? : </b></td>
                  <td>{this.state.lgl3}</td>
                </tr>
                <tr>
                  <td><b>Could you please briefly explain your family breakdown situation? : </b></td>
                  <td>{this.state.lgl4}</td>
                </tr>
                <tr>
                  <td><b>Are you facing two or more health concerns mentioned in bellow? : </b></td>
                  <td>{this.state.med1}</td>
                </tr>
                <tr>
                  <td><b>Can you briefly describe your health condition? : </b></td>
                  <td>{this.state.med2}</td>
                </tr>
                <tr>
                  <td><b>Are you addicted to any alcohol or drugs? : </b></td>
                  <td>{this.state.med3}</td>
                </tr>
              </tbody>
            </table>
          </ModalBody>
        </Modal>

        </div>
      );
    } else {
      return <Redirect to='/login' />
    }
  }
}

export default ChatList;